const initAnimation = () => {
  gsap.to('[data-animation-contacts="opacity"]', {
    opacity: 0,
    duration: 2,
    scrollTrigger: {
      trigger: '[data-animation-contacts="opacity"]', //     
      start: 'top 120px', //  ,       
      scrub: true, //  ,     .    
    },
  });
  gsap.to('[data-animation-contacts-header="opacity"]', {
    opacity: 1,
    pointerEvents: 'initial',
    duration: 2,
    startAt: {
      opacity: 0,
      pointerEvents: 'none',
    },
    scrollTrigger: {
      trigger: '[data-animation-contacts="opacity"]', //     
      start: 'top 60px', //  ,       
      scrub: true, //  ,     .    
    },
  });
}

export default initAnimation;
